import {
	api,
	postajax
} from '@/utils/api.js'
export default {
	data() {
		return {
			focus: 0,
			username: '',
			password: ''

		};
	},
	onLoad() {
		let loginParam = uni.getStorageSync('loginParam')
		this.username = loginParam.username;
		this.password = loginParam.password
	},
	methods: {
		focustxt(type) {
			this.focus = type;
		},
		blurtxt() {
			this.focus = 0;
		},
		login() {
			if (this.password.trim() && this.username.trim()) {
				var param = {
					username: this.username.trim(),
					password: this.password.trim()
				}
				uni.clearStorageSync();
				postajax(api.login, param).then((da) => {
					console.log(da)
					if (da.code == 0) {
						uni.setStorageSync('loginParam', param)
						uni.switchTab({
							url: '/pages/index/index'
						})

					}
				})
			} else {
				uni.showToast({
					title: '请输入正确的用户名和密码',
					icon: 'none'
				})
			}
		}
	}
};
